.filter-sidebar {
    width: 150px;
    height: 90%;
    float: left;
    padding: 1px;
    border: 2px solid black;
    position: fixed; /* Fixed Sidebar (stay in place on scroll) */

    background-color: beige; /* Black */
    overflow-x: hidden; /* Disable horizontal scroll */
    padding-top: 0%;
}

.grid {
    display: grid;
    width: 50%;
    height: 90%;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 10px;
    padding: 10px;
    margin-left: 150px;
    border: 2px solid black;
}

.card {
    border: 2px solid black;
    border-radius: 10px;
    padding: 5px;
    overflow: visible;
    cursor: pointer;
    position: relative;
}


    .card img {
        width: 100%;
        height: auto;
    }

